ion-header-bar {
  background-color: rgba(137, 107, 235, 0.7) !important; }

.item .item-content.activated {
  background-color: rgba(137, 107, 235, 0.9);
  border-color: rgba(137, 107, 235, 0.7);
  color: #FFF; }

.bar .title, .bar .buttons-left span, .bar .buttons-right span {
  color: #FFF !important; }

.modal {
  max-width: 800px;
  margin: 0 auto; }

.scroll-page .icon:before {
  margin-top: -13px;
  padding-right: 3px; }

.scroll-page .row + .row {
  margin-top: 0;
  padding: 0px; }

.centradoVertical {
  padding-top: 14px; }

.scroll-page .row:nth-of-type(odd) {
  background: rgba(137, 107, 235, 0.15); }

.scroll-page .row:nth-of-type(even) {
  background: rgba(137, 107, 235, 0.02); }

.item-radio input:checked + .radio-content .item-content {
  background: rgba(137, 107, 235, 0.15); }

.scroll-page .wind-icon {
  display: inline-block; }

.scroll-page.center {
  text-align: center; }

.scroll-page .has-header {
  position: relative; }

.iconoDiario {
  font-size: 40px;
  display: inline-block;
  width: 40px; }

.porcentajeIconoDiario {
  float: left;
  padding-left: 35px;
  top: -27.4px; }

.ion-content {
  overflow-scroll: false; }

.scroll-page .primary {
  margin: 0;
  font-size: 4em;
  font-weight: lighter; }

.scroll-page .secondary {
  margin: 0;
  font-size: 12em;
  font-weight: lighter; }

.loading-sign .spinner svg {
  width: 90px;
  height: 90px;
  stroke: #444;
  fill: #444;
  display: block;
  margin: auto; }

.loading-wrapper {
  position: absolute;
  background: rgba(255, 255, 255, 0.9); }

.loading-sign {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 50%;
  height: 50%;
  margin-top: 35%; }

.cg-busy-default-text {
  margin: 0 auto;
  font-size: 16px;
  color: #555;
  text-align: center;
  max-width: 400px;
  min-width: auto; }

.ocultar {
  color: #FFF; }

.sinScrollBar {
  overflow: hidden !important; }

.margenIzq {
  margin-left: 10px; }

.action-sheet-group.action-sheet-options button {
  text-align: center !important; }

.overflow-scroll {
  overflow-y: hidden; }

.boxAlarma {
  position: absolute;
  right: 0px;
  margin-right: 10px;
  margin-top: -60px;
  width: 60px;
  height: 60px;
  padding-top: 16px;
  padding-left: 4px;
  border-radius: 34px;
  background-color: rgba(137, 107, 235, 0.2);
  border: 3px solid rgba(137, 107, 235, 0.15);
  box-shadow: 2px 4px 7px #666; }
  .boxAlarma .iconoDiario {
    font-size: 32px !important; }

.boxAlarma:hover {
  box-shadow: 1px 3px 6px #666; }

.boxAlarma:active {
  box-shadow: 1px 2px 16px #666; }

.alertaActivada {
  padding-top: 10px;
  padding-left: 4px;
  background-color: rgba(137, 107, 235, 0.65);
  border: 3px solid rgba(137, 107, 235, 0.45); }
  .alertaActivada .iconoDiario {
    font-size: 58px !important; }

.menu .overflow-scroll {
  overflow-y: scroll !important; }

.config.overflow-scroll {
  overflow-y: scroll !important; }

.item-divider {
  background-color: rgba(137, 107, 235, 0.45); }

.barraModal {
  font-size: 12px !important;
  line-height: 20px !important;
  margin: 0px !important; }

.bar {
  border-top: 0px !important;
  border-bottom: 0px !important; }

.row.titulo .col {
  font-weight: 900; }

.itemArreglo {
  top: 50px !important; }

.superLunaRow {
  background-color: rgba(255, 28, 4, 0.21) !important; }

.superLunaCol {
  font-weight: bold;
  color: rgba(255, 4, 4, 0.8); }

.datosClima .item {
  text-align: center; }

@media (max-height: 440px) and (orientation: landscape) {
  .scroll-page .primary {
    font-size: 2em !important;
    margin: 0 auto !important; }
  .scroll-page .secondary {
    margin: 0;
    font-size: 16em;
    font-weight: lighter;
    position: fixed;
    left: 30px; }
  .weatherIcon {
    position: absolute;
    left: 4.2em;
    margin-top: 100px;
    height: 2.4em;
    font-size: 2.0em; }
  .carga {
    display: none; }
  .info {
    position: absolute;
    right: 25px;
    margin-top: 65px;
    font-size: 19px;
    line-height: 1.3em; }
  .col {
    padding: 0px !important; }
  .row {
    padding: 0px; }
  .iconoDiario {
    font-size: 30px; }
  .porcentajeIconoDiario {
    padding-left: 26px;
    top: -20.4px; }
  .boxAlarma {
    margin-top: -25px; } }

@media (min-height: 550px) {
  .scroll-page .primary {
    margin: 0;
    font-size: 45px;
    font-weight: lighter;
    padding: 0.5em 0.8em 0 0.8em; }
  .scroll-page .secondary {
    margin: 0;
    font-size: 190px;
    font-weight: lighter; }
  .porcentajeIconoDiario {
    float: left;
    padding-left: 27px;
    top: -28px; }
  .has-header p {
    font-size: 1.3em; }
  .row {
    padding: 0px !important; }
  .padding {
    padding: 0px !important;
    padding-left: 10px !important;
    padding-right: 10px !important; } }

@media (min-width: 900px) and (min-height: 700px) {
  .view {
    max-width: 900px;
    margin: 0 auto; }
  .scroll-page.center {
    max-width: 900px; }
  .bar .title {
    max-width: 900px; } }
